import React, { ChangeEvent } from "react";

import { useGetLocale } from "providers";

import { Input, Typography, RadioGroup } from "components";

import classes from "./style.module.scss";

export interface OperationValue {
  variant: string;
  betPercent: string;
}

export interface OperationProps {
  title: string;
  value: OperationValue;
  onChange: React.Dispatch<React.SetStateAction<OperationValue>>;
  disabled?: boolean;
}

export const Operation = ({
  title,
  value,
  onChange,
  disabled,
}: OperationProps) => {
  const intl = useGetLocale();

  const handleRadioChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    onChange((val) => ({ ...val, variant: event.target.value }));
  };

  const handleBetInputChange = (event: ChangeEvent<HTMLInputElement>) => {
    onChange((val) => ({ ...val, betPercent: event.target.value }));
  };

  return (
    <div className={classes.wrapper}>
      <Typography variant="h2" className={classes.title}>
        {title}
      </Typography>
      <div>
        <fieldset className={classes.formBox}>
          <RadioGroup
            value={value.variant}
            onChange={handleRadioChange}
            values={["0", "1", "2"]}
            labels={[
              intl.formatMessage({ id: "plinko.autoBet.changeBetBy" }),
              intl.formatMessage({
                id: "plinko.autoBet.resetToBaseBet",
              }),
              intl.formatMessage({ id: "plinko.autoBet.stopBetting" }),
            ]}
            name={`${title}_radiogroup`.replace(" ", "_")}
            nestedComponents={{
              1: (
                <div className={classes.inputWrapper}>
                  <Input
                    classes={{ root: classes.rootInput }}
                    onChange={handleBetInputChange}
                    value={value.betPercent}
                    type="number"
                    disabled={disabled}
                  />
                </div>
              ),
            }}
            radioClasses={{
              label: classes.radioLabel,
              round: classes.radioRound,
              radio: classes.radioRadio,
              root: classes.radioRoot,
            }}
          />
        </fieldset>
      </div>
    </div>
  );
};

export default Operation;
