import React from "react";
import { useGetLocale } from "providers";

import { SelectOption } from "components";
import { LabeledSelect, BetInput } from "pages/Plinko/components";
import { isNumberString } from "utils";

import classes from "./style.module.scss";

export interface ControllerManualProps {
  value: string;
  setValue: (value: string) => void;

  risk: string;
  onRiskChange: (value: string | number) => void;
  riskOptions: SelectOption[];

  rowsCount: string;
  onRowsCountChange: (value: string | number) => void;
  rowsCountOptions: SelectOption[];

  inputProps: {
    min: string;
    max: string;
    step: string;
  };
}

const ControllerManual = ({
  value,
  setValue,
  risk,
  onRiskChange,
  riskOptions,
  rowsCount,
  onRowsCountChange,
  rowsCountOptions,
  inputProps,
}: ControllerManualProps) => {
  const intl = useGetLocale();

  return (
    <div className={classes.inputsBlock}>
      <div className={classes.inputWrapper}>
        <BetInput
          id="plinko-value-input"
          className={classes.betInput}
          label={intl.formatMessage({ id: "plinko.bet.label" })}
          value={value}
          inputProps={inputProps}
          onChange={(value) => {
            if (isNumberString(value) || value === "") setValue(value);
          }}
        />
      </div>
      <div className={classes.selectWrapper}>
        <LabeledSelect
          id="plinko-risk-select"
          label={intl.formatMessage({ id: "plinko.risk.label" })}
          value={risk}
          onChange={onRiskChange}
          options={riskOptions}
        />
        <LabeledSelect
          id="plinko-rows-select"
          label={intl.formatMessage({ id: "plinko.rows.label" })}
          value={rowsCount}
          onChange={onRowsCountChange}
          options={rowsCountOptions}
        />
      </div>
    </div>
  );
};

export default React.memo(ControllerManual);
