import React, { useCallback, useEffect, useState } from "react";
import { useGetLocale } from "providers";
import clsx from "clsx";

import { Button, GetCoinImg, InputAdornment, Warning } from "components";
import { useCoins } from "hooks";

import { useHotKeyListener } from "components/HotKeys/Listener";
import InputBase, {
  BaseInputProps as InputBaseProps,
} from "components/Input/InputBase";

import classes from "./style.module.scss";

export interface BetInputProps extends Omit<InputBaseProps, "onChange"> {
  id?: string;
  label?: string;
  helperText?: string;
  inputProps: {
    min: string;
    max: string;
    step: string;
  };
  onChange: (val: string) => void;
  isAutoStarted?: boolean;
}

const defaultBalance = 0.000001;

const BetInput = React.forwardRef<HTMLInputElement, BetInputProps>(
  (
    {
      id,
      label,
      onChange: setValue,
      className,
      value,
      inputProps,
      isAutoStarted,
      ...props
    },
    ref,
  ) => {
    const intl = useGetLocale();
    const { selectedCoin, showInUSD } = useCoins(["selectedCoin", "showInUSD"]);
    const [warning, setWarning] = useState(false);

    const minValue = Number(inputProps.min) + Number(inputProps.step);
    const maxValue = Number(inputProps.max);
    const onChange = (value: string) => {
      setValue(value);
      localStorage.setItem("plinkoAmount", value);
    };

    useEffect(() => {
      let balanceVal: number;

      if (showInUSD && selectedCoin && selectedCoin?.coin !== "GEM") {
        balanceVal = Number(selectedCoin.balance) * Number(selectedCoin?.price);
      } else {
        balanceVal = Number(selectedCoin?.balance);
      }
      if (!isAutoStarted) {
        if (
          value &&
          selectedCoin &&
          balanceVal &&
          Number(value) <= balanceVal
        ) {
          const nsa = Number(value);
          if (nsa >= minValue && nsa <= maxValue) {
            onChange(`${value}`);
          }
        } else {
          setValue(String(defaultBalance));
        }
      }
    }, [selectedCoin]);

    const handleChange: InputBaseProps["onChange"] = (event) => {
      onChange(event.target.value);
    };

    const handleBlur: InputBaseProps["onBlur"] = (event) => {
      const value = event.target.value;
      const number = Number(value);
      const rounded = Number(number.toFixed(8));

      const isConvertToUsd =
        showInUSD && selectedCoin && selectedCoin?.coin !== "GEM";

      const convertedBalance = isConvertToUsd
        ? Number(selectedCoin?.price) * Number(selectedCoin?.balance)
        : Number(selectedCoin?.balance);

      if (rounded <= minValue) {
        onChange(String(minValue));
      } else if (rounded >= maxValue || rounded >= convertedBalance) {
        onChange(minValue.toFixed(8));
      } else {
        onChange(String(rounded));
      }
    };

    const handleMin = () => {
      const number = Number(inputProps.min) + Number(inputProps.step);

      onChange(number.toFixed(8));
    };

    const handleHalf = () => {
      const number = Number(value) / 2;
      const rounded = Number(number.toFixed(8));

      if (rounded <= minValue) {
        onChange(String(minValue));
      } else {
        onChange(String(rounded));
      }
    };

    const handleDouble = () => {
      const number = Number(value) * 2;
      const rounded = Number(number.toFixed(8));

      if (rounded >= maxValue) {
        onChange(String(maxValue));
      } else {
        onChange(String(rounded));
      }
    };

    const handleMax = () => {
      const number = Number(inputProps.max);
      let maxValue: number;
      if (showInUSD && selectedCoin && selectedCoin?.coin !== "GEM") {
        maxValue = Math.min(
          Number(selectedCoin?.balance) * Number(selectedCoin?.price),
          number,
        );
      } else {
        maxValue = Math.min(Number(selectedCoin?.balance), number);
      }

      onChange(maxValue ? maxValue.toFixed(8) : number.toFixed(8));
    };

    const handleWarningOpen = useCallback(() => {
      setWarning(true);
    }, []);

    const handleWarningClose = useCallback(() => {
      setWarning(false);
    }, []);

    useHotKeyListener({
      callbackKeyA: handleHalf,
      callbackKeyS: handleDouble,
    });

    return (
      <div className={classes.formInput}>
        {label && (
          <label htmlFor={id} className={classes.formInputLabel}>
            {label}
          </label>
        )}
        <InputBase
          {...props}
          ref={ref}
          id={id}
          value={value}
          onChange={handleChange}
          onBlur={handleBlur}
          className={clsx(className, classes.formInputField)}
          startAdornment={
            <InputAdornment
              position="start"
              className={classes.formInputStartAdornment}>
              <GetCoinImg />
              {showInUSD && selectedCoin?.coin !== "GEM" ? (
                <span className={classes.dollarLabel}>$</span>
              ) : null}
            </InputAdornment>
          }
          endAdornment={
            <InputAdornment
              position="end"
              className={classes.formInputAdornment}>
              <Button className={classes.adornmentButton} onClick={handleMin}>
                {intl.formatMessage({ id: "plinko.bet.buttons.min" })}
              </Button>
              <Button className={classes.adornmentButton} onClick={handleHalf}>
                {intl.formatMessage({ id: "plinko.bet.buttons.half" })}
              </Button>
              <Button
                className={classes.adornmentButton}
                onClick={handleDouble}>
                {intl.formatMessage({ id: "plinko.bet.buttons.double" })}
              </Button>
              <Button
                className={classes.adornmentButton}
                onClick={handleWarningOpen}>
                {intl.formatMessage({ id: "plinko.bet.buttons.max" })}
              </Button>

              <Warning
                isOpen={warning}
                onClose={handleWarningClose}
                callBack={handleMax}
              />
            </InputAdornment>
          }
        />
      </div>
    );
  },
);

export default BetInput;
