import React, {
  FunctionComponent,
  ChangeEvent,
  useEffect,
  useMemo,
  useRef,
  useState,
} from "react";
import { useGetLocale } from "providers";
import { throttle } from "utils/lodash";

import { PnlCalculator, repeat, track, toDecimal } from "utils";
import { InputProps, SelectOption } from "components";
import { coinsManager, useCoins } from "hooks";

import { ConfigurationData, PlayBetData } from "pages/Plinko/page";
import OptionsButton from "components/OptionsButton";
import { useSeed } from "providers/Seed";

import { OperationValue } from "../Operation";
import Buttons from "./Buttons";
import Manual from "./Manual";
import Auto from "./Auto";

import classes from "./style.module.scss";

const isDevelopment = process.env.NODE_ENV === "development";
const timeouts: NodeJS.Timeout[] = [];
const clearTimeouts = () => timeouts.forEach((t) => clearTimeout(t));

export interface ControllerProps {
  handleBet: (
    props: {
      amount: string;
      pins: string;
      risk: string;
      coin: string;
      seed: string;
    },
    clear?: () => void,
  ) => Promise<PlayBetData | undefined>;
  configurationData?: ConfigurationData;
  setMultipliers: (value: number[]) => void;
  handleLiveStatChange: () => void;
  handleHotKeysChange: () => void;
  handleBetInfoChange: () => void;
  handleSoundChange: () => void;
}

export const getRiskLevel = (value: string) => {
  switch (value) {
    case "1":
      return "low";
    case "3":
      return "high";
    default:
      return "medium";
  }
};

const updateBalanceWait = throttle((f: () => void) => {
  f();
}, 500);

const betWait = throttle((f: () => void) => {
  f();
}, 400);

const minimumTimeBetweenAutoBet = 400;

const pnlCalculator = new PnlCalculator();

const Controller: FunctionComponent<ControllerProps> = ({
  handleBet,
  configurationData,
  setMultipliers,
  handleHotKeysChange,
  handleLiveStatChange,
  handleBetInfoChange,
  handleSoundChange,
}) => {
  const intl = useGetLocale();
  const { selectedCoin, showInUSD } = useCoins();
  const { clientSeed, rotateClientSeed, updateNonce } = useSeed();

  const [value, setValue] = useState(
    localStorage.getItem("plinkoAmount") || "0",
  );
  const [autoBetValue, setAutoBetValue] = useState(value);
  const autoBetValueRef = useRef(value);

  const [risk, setRisk] = useState("2");
  const [rowsCount, setRowsCount] = useState("12");
  const [isAutoBet, setIsAutoBet] = useState(false);
  const [isStartedAutoBet, setIsStartedAutoBet] = useState(false);

  const [numberOfBets, setNumberOfBets] = useState("0");
  const [autoNumberOfBets, setAutoNumberOfBets] = useState(numberOfBets);
  const autoNumberOfBetsRef = useRef(Number(numberOfBets));
  const savedHandler = useRef<(...args: any[]) => void>();
  const [isInit, setIsInit] = useState(false);

  const [stopOnLoss, setStopOnLoss] = useState("");
  const [stopOnProfit, setStopOnProfit] = useState("");

  const [winOperation, setWinOperation] = useState<OperationValue>({
    variant: "0",
    betPercent: "0",
  });
  const [lossOperation, setLossOperation] = useState<OperationValue>({
    variant: "0",
    betPercent: "0",
  });

  const calculateMinCeil = (value: string, price: string) => {
    return (+(
      Math.ceil(Number(value) * Number(price) * 100000000) / 100000000
    )).toFixed(8);
  };

  const inputProps = useMemo(() => {
    const defaultCoin = {
      minimum_bet: "0.00000010",
      maximum_bet: "10000000",
    };
    let currentCoin = defaultCoin;

    if (configurationData && selectedCoin?.coin) {
      currentCoin =
        configurationData.coins.find(
          ({ coin }) => coin === selectedCoin.coin,
        ) || defaultCoin;
    }

    if (showInUSD && selectedCoin && selectedCoin?.coin !== "GEM") {
      return {
        min: "0.00000000",
        max: (
          Number(currentCoin.maximum_bet) * Number(selectedCoin?.price)
        ).toString(),
        step: calculateMinCeil(
          currentCoin.minimum_bet,
          selectedCoin?.price || "0",
        ),
      };
    }

    return {
      min: "0.00000000",
      max: currentCoin.maximum_bet,
      step: currentCoin.minimum_bet,
    };
  }, [selectedCoin?.coin, configurationData, value, showInUSD]);

  useEffect(() => {
    if (!configurationData) {
      return;
    }

    const currentTable =
      configurationData.multipliers_table[rowsCount][getRiskLevel(risk)];

    setMultipliers(currentTable);
  }, [configurationData, risk, rowsCount, setMultipliers]);

  const riskOptions = useMemo<SelectOption[]>(
    () => [
      {
        value: "1",
        label: intl.formatMessage({ id: "plinko.risk.low" }),
      },
      {
        value: "2",
        label: intl.formatMessage({ id: "plinko.risk.average" }),
      },
      {
        value: "3",
        label: intl.formatMessage({ id: "plinko.risk.high" }),
      },
    ],
    [intl],
  );

  const rowsCountOptions = useMemo<SelectOption[]>(() => {
    return repeat<SelectOption>(9, (index) => {
      const value = index + 8;
      return {
        value: `${value}`,
        label: `${value}`, // intl.formatMessage({ id: "plinko.rows.count" },{ count: value },),
      };
    });
  }, []);

  const handleRiskChange = (value: string | number) => {
    if (typeof value !== "string") {
      return;
    }

    setRisk(value);
  };

  const handleRowsCountChange = (value: string | number) => {
    if (typeof value !== "string") {
      return;
    }

    setRowsCount(value);
  };

  const handleStopOnChange = (
    event: ChangeEvent<HTMLInputElement | HTMLTextAreaElement>,
    field: "profit" | "loss",
  ) => {
    const value = Number(event.target.value);
    if (isNaN(value) || !isFinite(value) || value < 0) return;
    field === "loss"
      ? setStopOnLoss(event.target.value)
      : setStopOnProfit(event.target.value);
  };

  useEffect(() => {
    setAutoBetValue(value);
    autoBetValueRef.current = value;

    if (!isStartedAutoBet) {
      pnlCalculator.resetPnl();
      pnlCalculator.block = true;
      return;
    }
    pnlCalculator.block = false;
  }, [value, isStartedAutoBet]);

  useEffect(() => {
    setAutoNumberOfBets(numberOfBets);
  }, [numberOfBets, isStartedAutoBet]);

  const clearCurrentInterval = () => {
    autoNumberOfBetsRef.current = Number(numberOfBets);
    savedHandler.current = undefined;
    setAutoBetValue(value);
    autoBetValueRef.current = value;

    pnlCalculator.resetPnl();

    setAutoNumberOfBets(numberOfBets);
    setIsStartedAutoBet(false);
  };

  const updateBalance = (balance: string, coin: string) => {
    updateBalanceWait(() => {
      coinsManager.coinValueUpdate(balance, coin);
    });
  };

  const submit = () => {
    const handle = async () => {
      if (!selectedCoin || !clientSeed) {
        return;
      }

      const coin = selectedCoin?.coin || "BTC";

      let amount =
        showInUSD && selectedCoin.coin !== "GEM"
          ? (
              Number(autoBetValueRef.current) / Number(selectedCoin?.price)
            ).toFixed(8)
          : autoBetValueRef.current;

      const result = await handleBet(
        {
          amount: amount,
          pins: rowsCount,
          risk: risk,
          coin,
          seed: clientSeed?.seed || "",
        },
        clearCurrentInterval,
      );

      if (!clientSeed?.isSetManually) {
        rotateClientSeed();
      } else {
        updateNonce();
      }

      if (!result || !result.success) {
        return;
      }

      pnlCalculator.addProfit(
        toDecimal(result.payout).mul(amount).minus(amount),
      );

      updateBalance(result.balance, coin);

      return result;
    };

    return handle();
  };

  const handleSubmit = () => {
    betWait(() => {
      if (isAutoBet) {
        setIsStartedAutoBet((val) => !val);
      } else {
        submit();
      }
    });
  };

  const handleSwitch = () => {
    setIsAutoBet((value) => !value);
  };

  const handleChange: InputProps["onChange"] = (event) => {
    const val = event.target.value;

    if (Number(val) < 0) {
      autoNumberOfBetsRef.current = 0;
      setNumberOfBets("0");
      return;
    }

    const number = event.target.value.replace(/[^0-9]*/g, "");

    autoNumberOfBetsRef.current = Number(number);
    setNumberOfBets(number);
  };

  const handleOperation = (operation: OperationValue): boolean => {
    const betPercent = Number(operation.betPercent);
    if (operation.variant === "0") {
      setAutoBetValue(() => {
        const valNumber = Number(autoBetValueRef.current);
        const changeAmount = (valNumber / 100) * betPercent;
        const currentValueNumber = valNumber + changeAmount;

        if (Number(inputProps.step) > currentValueNumber) {
          return inputProps.step;
        }

        if (currentValueNumber > Number(inputProps.max)) {
          return inputProps.max;
        }

        const output = `${currentValueNumber.toFixed(8)}`;
        autoBetValueRef.current = output;

        return output;
      });

      return true;
    } else if (operation.variant === "1") {
      setAutoBetValue(value);
      autoBetValueRef.current = value;
    } else {
      clearCurrentInterval();
      return false;
    }
    return true;
  };

  const tickHandler = async () => {
    if (
      Number(numberOfBets) &&
      (!autoNumberOfBetsRef.current || autoNumberOfBetsRef.current <= 0)
    ) {
      clearCurrentInterval();
      return;
    } else {
      autoNumberOfBetsRef.current = autoNumberOfBetsRef.current - 1;
    }

    if (isStartedAutoBet && Number(numberOfBets)) {
      setAutoNumberOfBets((val) => {
        const numberValuer = Number(val);
        const currentValue = numberValuer - 1;

        if (currentValue || currentValue === 0) {
          return `${currentValue}`;
        }

        return val;
      });
    }

    try {
      const startTime = Date.now();

      const result = await submit();

      if (!result) {
        return;
      }

      if (!isStartedAutoBet) {
        return;
      }

      let continueLoop = true;

      if (result.win) {
        continueLoop = handleOperation(winOperation);
      } else {
        continueLoop = handleOperation(lossOperation);
      }

      if (
        continueLoop &&
        (pnlCalculator.isPnlOverLimit(stopOnLoss, "loss") ||
          pnlCalculator.isPnlOverLimit(stopOnProfit, "profit"))
      ) {
        clearCurrentInterval();
        continueLoop = false;
      }

      if (savedHandler.current && continueLoop) {
        const timeDelta = Date.now() - startTime;
        if (timeDelta > minimumTimeBetweenAutoBet) {
          savedHandler.current();
        } else {
          const t = setTimeout(() => {
            if (savedHandler.current) {
              savedHandler.current();
            }
          }, minimumTimeBetweenAutoBet - timeDelta);
          timeouts.push(t);
        }
      }
    } catch (error) {
      if (isDevelopment) {
        console.log("Controller error", error);
      }

      const message = `
    ${error}
    Plinko > Controller > tickHandler`;

      try {
        track()?.errors(message);
      } catch {
        console.log(" trackError", error);
      }
    }
  };
  savedHandler.current = tickHandler;

  useEffect(() => {
    return () => {
      savedHandler.current = undefined;
    };
  }, []);

  useEffect(() => {
    if (isStartedAutoBet && savedHandler.current) {
      savedHandler.current();
    }
  }, [isStartedAutoBet, setIsStartedAutoBet]);

  useEffect(() => {
    if (!configurationData || !selectedCoin?.coin) {
      return;
    }

    const currentCoin = configurationData.coins.find(
      ({ coin }) => coin === selectedCoin.coin,
    );

    if (!currentCoin) {
      return;
    }

    let amount = Number(autoBetValueRef.current);
    const max =
      showInUSD && selectedCoin?.coin !== "GEM"
        ? Number(currentCoin.maximum_bet) * Number(selectedCoin?.price)
        : Number(currentCoin.maximum_bet);
    const min =
      showInUSD && selectedCoin?.coin !== "GEM"
        ? calculateMinCeil(currentCoin.minimum_bet, selectedCoin?.price || "0")
        : Number(currentCoin.minimum_bet);

    if (amount > max) {
      let number, maxValue;
      if (showInUSD && selectedCoin?.coin !== "GEM") {
        number = Number(currentCoin.maximum_bet) * Number(selectedCoin?.price);
        maxValue = Math.min(
          Number(selectedCoin?.balance) * Number(selectedCoin?.price),
          number,
        );
      } else {
        number = Number(currentCoin.maximum_bet);
        maxValue = Math.min(Number(selectedCoin?.balance), number);
      }

      const maxValueString = maxValue ? maxValue.toFixed(8) : number.toFixed(8);

      autoBetValueRef.current = maxValueString;
      setAutoBetValue(maxValueString);

      setValue(maxValueString);
    } else if (amount < min) {
      if (showInUSD && selectedCoin?.coin !== "GEM") {
        const minBet = calculateMinCeil(
          currentCoin.minimum_bet,
          selectedCoin?.price || "0",
        );
        autoBetValueRef.current = minBet;
        setAutoBetValue(minBet);
        setValue(minBet);
      } else {
        autoBetValueRef.current = currentCoin.minimum_bet;
        setAutoBetValue(currentCoin.minimum_bet);
        setValue(currentCoin.minimum_bet);
      }
    }
    clearCurrentInterval();
    clearTimeouts();
  }, [selectedCoin?.coin, configurationData, showInUSD]);

  useEffect(() => {
    if (!isInit) {
      setIsInit(true);
      return;
    }
    if (!configurationData || !selectedCoin?.coin) {
      return;
    }
    if (selectedCoin.coin === "GEM") {
      return;
    }
    const currentCoin = configurationData.coins.find(
      ({ coin }) => coin === selectedCoin.coin,
    );
    if (!currentCoin) {
      return;
    }
    let bet: string;
    let min = currentCoin.minimum_bet;
    if (showInUSD) {
      bet = (Number(value) * Number(selectedCoin?.price)).toFixed(8);
      min = calculateMinCeil(
        currentCoin.minimum_bet,
        selectedCoin?.price || "0",
      );
    } else {
      bet = (Number(value) / Number(selectedCoin?.price)).toFixed(8);
    }
    const val = Number(min) > Number(bet) ? min : bet;
    autoBetValueRef.current = val;
    setAutoBetValue(val);
    setValue(val);
  }, [showInUSD]);

  return (
    <div className={classes.wrapper}>
      <div className={classes.innerWrapper}>
        <Buttons
          value={isAutoBet}
          onChange={handleSwitch}
          onSubmit={handleSubmit}
          isStartedAutoBet={isStartedAutoBet}
        />
        <OptionsButton
          withSound
          soundOnClick={handleSoundChange}
          liveStatOnclick={handleLiveStatChange}
          hotKeysOnClick={handleHotKeysChange}
          bettingInformationOnClick={handleBetInfoChange}
        />

        <div className={classes.autoBetWrapper}>
          {isAutoBet ? (
            <Auto
              value={autoBetValue}
              setValue={setValue}
              risk={risk}
              onRiskChange={handleRiskChange}
              riskOptions={riskOptions}
              rowsCount={rowsCount}
              onRowsCountChange={handleRowsCountChange}
              rowsCountOptions={rowsCountOptions}
              inputProps={inputProps}
              numberOfBets={autoNumberOfBets}
              onNumberOfBetsChange={handleChange}
              winOperation={winOperation}
              setWinOperation={setWinOperation}
              lossOperation={lossOperation}
              setLossOperation={setLossOperation}
              isStartedAutoBet={isStartedAutoBet}
              onStopOnLossChange={(event) => handleStopOnChange(event, "loss")}
              onStopOnProfitChange={(event) =>
                handleStopOnChange(event, "profit")
              }
              stopOnLoss={stopOnLoss}
              stopOnProfit={stopOnProfit}
            />
          ) : (
            <div className={classes.manualControllerWrapper}>
              <Manual
                value={value}
                setValue={setValue}
                risk={risk}
                onRiskChange={handleRiskChange}
                riskOptions={riskOptions}
                rowsCount={rowsCount}
                onRowsCountChange={handleRowsCountChange}
                rowsCountOptions={rowsCountOptions}
                inputProps={inputProps}
              />
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default React.memo(Controller);
