import React from "react";
import { useGetLocale } from "providers";

import { SwitchBox, notifyBarManager } from "components";
import { SubmitButton } from "pages/Plinko/components";
import { useLocalStorage } from "hooks";
import { throttle } from "utils";
import { useHotKeyListener } from "components/HotKeys/Listener";

import classes from "./style.module.scss";

const wait = throttle();

export interface ButtonsProps {
  onSubmit: () => void;
  value: boolean;
  onChange: () => void;
  isStartedAutoBet: boolean;
}

const switchId = "plinko-auto-bet-switch";

const ControllerButtons = ({
  onSubmit,
  value,
  onChange,
  isStartedAutoBet,
}: ButtonsProps) => {
  const [token] = useLocalStorage("token");
  const intl = useGetLocale();

  const getButtonLabel = () => {
    if (isStartedAutoBet) {
      return intl.formatMessage({ id: "plinko.autoBet.stopLabel" });
    }

    if (value) {
      return intl.formatMessage({ id: "plinko.autoBet.startLabel" });
    }

    return intl.formatMessage({ id: "plinko.bet.label" });
  };

  const handleSubmit = () => {
    wait(() => {
      if (!token) {
        notifyBarManager.error({
          message: intl.formatMessage({ id: "plinko.bet.notLoggedIn" }),
        });
        return;
      }

      onSubmit();
    }, 250);
  };

  useHotKeyListener({
    callbackSpace: handleSubmit,
  });
  return (
    <div className={classes.wrapper}>
      <label className={classes.textWrapper} htmlFor={switchId}>
        <SwitchBox
          isChecked={value}
          text={{
            off: intl.formatMessage({ id: "plinko.bet.manual" }),
            on: intl.formatMessage({ id: "plinko.bet.auto" }),
          }}
          switchHandleChange={onChange}
          toggleSwitchPosition={"75%"}
        />
      </label>

      <SubmitButton onClick={handleSubmit}>{getButtonLabel()}</SubmitButton>
    </div>
  );
};

export default ControllerButtons;
