import React from "react";
import { SelectOption } from "components";

import { BaseSelect, SelectItem } from "components";

import classes from "./style.module.scss";

export interface LabeledSelectProps {
  id?: string;
  label: string;
  value: string;
  onChange: (value: number | string) => void;
  options: SelectOption[];
  disabled?: boolean;
}

const LabeledSelect = ({
  id,
  label,
  options,
  disabled,
  value,
  onChange,
}: LabeledSelectProps) => {
  return (
    <div className={classes.formInput}>
      <label htmlFor={id} className={classes.formInputLabel}>
        {label}
      </label>
      <BaseSelect
        onChange={onChange}
        value={options?.find((option) => option.value === value)?.label || ""}
        openFromUpwards={false}
        disabled={disabled}
        className={classes.select}
        classes={{
          label: classes.selectLabel,
          expandIcon: classes.selectExpandIcon,
        }}>
        {options?.map(({ value, label }) => {
          return (
            <SelectItem
              key={value}
              value={value}
              label={label || ""}
              className={classes.menuItem}
            />
          );
        })}
      </BaseSelect>
    </div>
  );
};

export default LabeledSelect;
