import React from "react";
import { importedLibraryDefault } from "react-loadable-library";

import LazyComponentLoader from "config/LazyComponentLoader";
const PlinkoEngineLazy = importedLibraryDefault(() =>
  LazyComponentLoader(import("games/Plinko")),
);

const GameWrapper = ({ setEnginRef }: any) => {
  return (
    <PlinkoEngineLazy>
      {(PlinkoEngine) => {
        setEnginRef(() => PlinkoEngine);
        return null;
      }}
    </PlinkoEngineLazy>
  );
};

export default GameWrapper;
