import React, { FunctionComponent } from "react";
import { useGetLocale } from "providers";
import { ReactComponent as AllInclusiveIcon } from "assets/icons/all-inclusive.svg";
import clsx from "clsx";

import { SelectOption } from "components";
import { LabeledSelect, BetInput } from "pages/Plinko/components";
import { isNumberString } from "utils";

import Input, { InputProps } from "../Input";
import Operation, { OperationValue } from "../../Operation";
import classes from "./style.module.scss";

export interface ControllerManualProps {
  value: string;
  setValue: (value: string) => void;

  risk: string;
  onRiskChange: (value: string | number) => void;
  riskOptions: SelectOption[];

  rowsCount: string;
  onRowsCountChange: (value: string | number) => void;
  rowsCountOptions: SelectOption[];

  numberOfBets: string;
  onNumberOfBetsChange: InputProps["onChange"];

  stopOnLoss: string;
  onStopOnLossChange: InputProps["onChange"];

  stopOnProfit: string;
  onStopOnProfitChange: InputProps["onChange"];

  inputProps: {
    min: string;
    max: string;
    step: string;
  };

  winOperation: OperationValue;
  setWinOperation: React.Dispatch<React.SetStateAction<OperationValue>>;

  lossOperation: OperationValue;
  setLossOperation: React.Dispatch<React.SetStateAction<OperationValue>>;
  isStartedAutoBet: boolean;
}

const ControllerManual: FunctionComponent<ControllerManualProps> = ({
  value,
  setValue,
  risk,
  onRiskChange,
  riskOptions,
  rowsCount,
  onRowsCountChange,
  rowsCountOptions,
  inputProps,

  numberOfBets,
  onNumberOfBetsChange,

  stopOnLoss,
  onStopOnLossChange,
  stopOnProfit,
  onStopOnProfitChange,

  winOperation,
  setWinOperation,
  lossOperation,
  setLossOperation,
  isStartedAutoBet,
  ...props
}) => {
  const intl = useGetLocale();

  return (
    <div {...props} className={classes.inputsWrapper}>
      <div className={classes.inputWrapper}>
        <BetInput
          id="plinko-value-input"
          className={classes.betInput}
          label={intl.formatMessage({ id: "plinko.bet.label" })}
          value={value}
          inputProps={inputProps}
          onChange={(value) => {
            if (isNumberString(value) || value === "") setValue(value);
          }}
          disabled={isStartedAutoBet}
          isAutoStarted={isStartedAutoBet}
        />
      </div>
      <div className={classes.selectWrapper}>
        <LabeledSelect
          id="plinko-risk-select"
          label={intl.formatMessage({ id: "plinko.risk.label" })}
          value={risk}
          onChange={onRiskChange}
          options={riskOptions}
          disabled={isStartedAutoBet}
        />
        <LabeledSelect
          id="plinko-rows-select"
          label={intl.formatMessage({ id: "plinko.rows.label" })}
          value={rowsCount}
          onChange={onRowsCountChange}
          options={rowsCountOptions}
          disabled={isStartedAutoBet}
        />
        <Input
          id="plinko-number-of-bets"
          label={intl.formatMessage({ id: "plinko.autoBet.numberOfBets" })}
          className={clsx(classes.betsCountInput, {
            [classes.betsCountInputPadding]:
              !numberOfBets || numberOfBets === "0",
          })}
          value={numberOfBets}
          onChange={onNumberOfBetsChange}
          type="number"
          endAdornment={
            <>
              {(!numberOfBets || numberOfBets === "0") && (
                <AllInclusiveIcon className={classes.infinityIcon} />
              )}
            </>
          }
          disabled={isStartedAutoBet}
        />
      </div>
      <div className={classes.stopOnWrapper}>
        <Input
          id="plinko-stop-on-profit"
          label="Stop on Profit"
          className={classes.betsCountInput}
          value={stopOnProfit}
          onChange={onStopOnProfitChange}
          disabled={isStartedAutoBet}
        />
        <Input
          id="plinko-stop-on-loss"
          label="Stop on Loss"
          className={classes.betsCountInput}
          value={stopOnLoss}
          onChange={onStopOnLossChange}
          disabled={isStartedAutoBet}
        />
      </div>
      <div>
        <div className={classes.operationContainer}>
          <div>
            <Operation
              title={intl.formatMessage({ id: "plinko.autoBet.onWin" })}
              value={winOperation}
              onChange={setWinOperation}
              disabled={isStartedAutoBet}
            />
          </div>
          <div>
            <Operation
              title={intl.formatMessage({ id: "plinko.autoBet.onLoss" })}
              value={lossOperation}
              onChange={setLossOperation}
              disabled={isStartedAutoBet}
            />
          </div>
        </div>
      </div>
    </div>
  );
};

export default ControllerManual;
